import { createElement } from '../render';
import { createEditEventTemplate } from './trip-events-edit-view';

const EVENTS_DATA = [
  {
    day: 'MAR 18',
    icon: 'img/icons/taxi.png',
    event: 'Taxi Amsterdam',
    time: '10:30',
    endTime: '11:00',
    duration: '30M',
    price: '20',
    offers: ['Order Uber +â‚¬  20'],
    star: true,
    isEdit: true,
  },
  {
    day: 'MAR 18',
    icon: 'img/icons/flight.png',
    event: 'Flight Chamonix',
    time: '12:25',
    endTime: '13:35',
    duration: '01H 10M',
    price: '160',
    offers: [
      {title: 'Add luggage', price: '50'},
      {title: 'Switch to comfort', price: '80'}
    ],
    star: false,
    isEdit: false,
  },
  {
    day: 'MAR 18',
    icon: 'img/icons/drive.png',
    event: 'Drive Chamonix',
    startTime: '14:30',
    endTime: '16:05',
    duration: '01H 35M',
    price: '160',
    offers: [{title: 'Rent a car', price: '200'}],
    star: true,
    isEdit: false,
  },
];

const createEventTemplate = (item) => `<li class="trip-events__item">
${item.isEdit ? createEditEventTemplate() : `
<div class="event">
  <time class="event__date" datetime="2019-03-18">${item.day}</time>
  <div class="event__type">
    <img class="event__type-icon" width="42" height="42" src="${item.icon}" alt="Event type icon">
  </div>
  <h3 class="event__title">${item.event}</h3>
  <div class="event__schedule">
    <p class="event__time">
      <time class="event__start-time" datetime="2019-03-18T10:30">${item.startTime}</time>
      &mdash;
      <time class="event__end-time" datetime="2019-03-18T11:00">${item.endTime}</time>
    </p>
    <p class="event__duration">${item.duration}</p>
  </div>
  <p class="event__price">
    &euro;&nbsp;<span class="event__price-value">${item.price}</span>
  </p>
  <h4 class="visually-hidden">Offers:</h4>
  <ul class="event__selected-offers">
  ${item.offers.map((offersItem) => `
  <li class="event__offer">
  <span class="event__offer-title">${offersItem.title}</span>
  &plus;&euro;&nbsp;
  <span class="event__offer-price">${offersItem.price}</span>
</li>
  `).join('')}
  </ul>
  <button class="event__favorite-btn event__favorite-btn--active" type="button">
    <span class="visually-hidden">Add to favorite</span>
    <svg class="event__favorite-icon" width="28" height="28" viewBox="0 0 28 28">
      <path d="M14 21l-8.22899 4.3262 1.57159-9.1631L.685209 9.67376 9.8855 8.33688 14 0l4.1145 8.33688 9.2003 1.33688-6.6574 6.48934 1.5716 9.1631L14 21z"/>
    </svg>
  </button>
  <button class="event__rollup-btn" type="button">
    <span class="visually-hidden">Open event</span>
  </button>
</div>
</li>
`}
`;

function createTripEventsTemplate() {
  return `<ul class="trip-events__list">
  ${EVENTS_DATA.map((item) => createEventTemplate(item)).join('')}
</ul>`;
}

export default class TripEventsView {
  getTemplate() {
    return createTripEventsTemplate();
  }

  getElement() {
    if (!this.element) {
      this.element = createElement(this.getTemplate());
    }
    return this.element;
  }

  removeElement() {
    this.element = null;
  }
}
